import fetch from 'isomorphic-unfetch';
import AppBar from '@material-ui/core/AppBar';
import ReactMarkdown from 'react-markdown';

class Skills extends React.Component {
  getSkillsByDomain = (domain) => {
    console.log(`${strapiUrl}/competences?domaines={_id:${domain}}`);
    const res = fetch(`${strapiUrl}/competences?domaines={_id:${domain}}`);
    const domains = res.json();

    return <div>
      {domain}
    </div>
  };

  render() {
    const { classes, hp, lang, domains } = this.props;

    return (
      <div id="section-skills">
        <AppBar position="static" className={classes.appbar}>
          {hp['Titre compétences']}
        </AppBar>
        <ReactMarkdown source={hp['Texte compétences']} className={classes.markdownskills} />
        {domains.map(domain => (
          <div key={domain._id}>
            <h2>{lang === 'en' ? domain['Titre anglais'] : domain['Titre']}</h2>
            <div>{this.getSkillsByDomain(domain._id)}</div>
          </div>
        ))}
      </div>
    );
  }
}

export default Skills;
